import React, { Component } from "react";
import a from "../assets/gambar/file_1481423289.jpg";
import b from "../assets/gambar/file_1481423323.jpg";
import c from "../assets/gambar/file_1481423391.jpg";

class home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      barang: [
        {
          gambar: a,
          nama: "Kalung merah",
          harga: "Rp. 20.000",
          diskon: "30",
          detail: "Detail Kalung Merah",
        },
        {
          gambar: b,
          nama: "Mainan anting",
          harga: "Rp. 15.000",
          diskon: "30",
          detail: "Detail Mainan Anting",
        },
        {
          gambar: c,
          nama: "Gelang Kuning",
          harga: "Rp. 18.000",
          diskon: "30",
          detail: "Detail gelang kuning",
        },
      ],
    };
  }
  render() {
    const { barang } = this.state;
    return (
      <>
        <div className="container row">
          <div
            class="col s12 card-panel center red"
            style={{
              fontSize: "1.5em",
              fontWeight: 600,
              color: "#fff",
            }}
          >
            Hot Promo !
          </div>

          <div className="col s12">
            <div className="row">
              {barang.map((data, key) => (
                <div className="col s12 m4 ">
                  <div class="card hoverable">
                    <div class="card-image waves-effect waves-block waves-light">
                      <img class="activator" src={data.gambar} alt="gambar" />
                    </div>
                    <div class="card-content">
                      <span class="card-title activator grey-text text-darken-4">
                        {data.nama}
                        <i class="material-icons right">more_vert</i>
                      </span>
                      <div>{data.harga}</div>
                      <p style={{ color: "red" }}>
                        <b>Diskon</b> {data.diskon}%
                      </p>
                    </div>
                    <div class="card-reveal">
                      <span class="card-title grey-text text-darken-4">
                        {data.nama}
                        <i class="material-icons right">close</i>
                      </span>
                      <p>{data.detail}</p>
                    </div>
                    <div class="card-action">
                      <div
                        style={{
                          display: "flex",
                          justifyContent: "space-between",
                        }}
                      >
                        <a href="#" className="btn green">
                          <i className="material-icons left">attach_money</i>
                          Beli
                        </a>

                        <a href="#" className="btn red">
                          Cart
                          <i className="material-icons right">
                            add_shopping_cart
                          </i>
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default home;
